<template>
  <div>
    <router-link :to="{path: `/todoGroupList/${todo.group}`}">
      <button class="btn w-100 text-left p-0" v-if="!todosAllIsCheck" type="button" data-toggle="collapse" :data-target="`#${todo.id}`" aria-expanded="false" :aria-controls="`${todo.id}`">
        <label for="dateline" class="col-form-label">
          <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="folder-open" />
          {{todo.group}}
        </label>
      </button>
    </router-link>
    <!-- class 加上 show 就可以打開 collapse-->
    <div :id="`${todo.id}`" class="collapse multi-collapse pl-3">
      <template v-for="item in todo.todos">
        <div :key="item.id" v-if="!item.isCheck" class="todo-row-wrap p-2 my-1 d-flex align-items-center" :class="{ lock: item.isCheck }">
          <div class="todo-row-card">
            <div class="form-check pl-3 mb-0">
              <font-awesome-icon class="ml-auto text-dark" size="lg" :icon="changeIconsByCheck(item.isCheck)" @click="item.isCheck = !item.isCheck"/>
              <router-link :to="{path: `/todoEdit/${item.id}`}" class="text-dark">
                <label class="form-check-label pl-2">
                  {{item.name}}
                </label>
              </router-link>
            </div>
          </div>
          <font-awesome-icon class="ml-auto" :class="{'text-primary':item.isImportant}" size="lg" :icon="changeIconsByImportant(item.isImportant)" @click="item.isImportant = !item.isImportant"/>
        </div>
      </template>
    </div>
    <hr>
  </div>
</template>

<script>
export default {
  props: {
    todo: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      isImportant: false,
      isCheck: false
    }
  },
  computed: {
    todosAllIsCheck () {
      let todo = this.todo.todos
      todo = todo.filter(item => item.isCheck === true)
      return todo.length === this.todo.todos.length
    }
  },
  methods: {
    changeIconsByImportant (isImportant) {
      if (isImportant) {
        return ['fas', 'star']
      }
      return ['far', 'star']
    },
    changeIconsByCheck (isCheck) {
      if (isCheck) {
        return ['fas', 'check-square']
      }
      return ['far', 'square']
    }
  }
}
</script>

<style>

</style>



